/**
 * 일단 auth0을 사용하지 않고 직접 서버접속으로 login하는 기능을 적용
 * auth0사용은 authServiceUsingAuth0.js파일 사용
 */
import EventEmitter from 'events'
//import authConfig from '@/../auth_config.json'

import router from '@/router'

// 'loggedIn' is used in other parts of application. So, Don't forget to change there also
const localStorageKey = 'loggedIn'

const tokenExpiryKey = 'tokenExpiry'
const loginEvent = 'loginEvent'

//const redirectUri = `${window.location.origin + process.env.BASE_URL}callback`
// const webAuth = new auth0.WebAuth({
//   domain: authConfig.domain,
//   redirectUri: `${window.location.origin + process.env.BASE_URL}callback`,
//   clientID: authConfig.clientId,
//   responseType: 'id_token',
//   scope: 'openid profile email'
// })
/**
 * 로그세션유지 방식:
 * 최초 화면열때 App.vue에서 isUserLoggedIn 확인
 * if isUserLoggedIn
 *    renewToken
 * else
 *    login화면
 * 
 * isUserLoggedIn:
 * - storage에 없거나 로그인주기 만료 여부
 * 
 * logout:
 * - storage삭제
 * - state삭제
 * 
 * login:
 * - invalidateSession
 * - 로그인 및 storage 추가
 * 
 * renewToken:
 * - storage업데이트
 * - 
 *  
 *   
 */
class AuthService extends EventEmitter {
    idToken = null;
    profile = null;
    tokenExpiry = null;

    // Starts the user login flow//
    // 로그인 페이지로 이동
    // login (customState) {
    //   //TODO
    // }

    /**
     * @param {*} authResult 
     * authResult.idToken 토큰 사용안함
     * authResult.idTokenPayLoad 사용자 정보
     */
   

    cleanseLoginInfo() {      
      localStorage.removeItem(localStorageKey)
      localStorage.removeItem(tokenExpiryKey)
      localStorage.removeItem('userInfo')
      localStorage.removeItem('accessToken')
      this.tokenExpiry = null
      this.profile = null
    }

    logOut () {
      console.log("logOut")
      this.cleanseLoginInfo()
      this.emit(loginEvent, { loggedIn: false })
      //store.commit('auth/SET_LOGOUT', {})
      //store.dispatch('auth/logout', {})
      router.push('/login').catch(() => {})
    }

    /**
     * Date.now() --> 1590010036432
     * 60초 -> 60000
     */
    isAuthenticated () {
      let hasExpKey = localStorage.getItem(tokenExpiryKey)
      let result = hasExpKey 
                  && new Date(Date.now()) < new Date(localStorage.getItem(tokenExpiryKey)) 
                  && localStorage.getItem(localStorageKey) === 'true'
      //console.log(`isAuthenticated result: ${new Date(Date.now()) < new Date(localStorage.getItem(tokenExpiryKey))} ${localStorage.getItem(localStorageKey)}`)
      return result
    }
}
console.log("=================================================")
console.log("AuthService")
console.log("=================================================")

export default new AuthService()